body{
  background-color: #f0efe9;
}
.App{
  background-color: #ffffff;
  min-height: 800px;
  width: 400px; 
  margin: 40px auto;
  border-radius: 50px;
  border-style:solid;
  border-color: #50e3a4;
}
.App h1{
  text-align: center;
  color: #50e3a4;
  font-family: fantasy;
  padding: 10px 0;
  margin: 0px;
  font-size: 50px;
  
}
.App hr{
  border-style:solid;
  border-color: #50e3a4;
}

#to-do-form input{
  background-color: #dfdfdf;
  border:0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius:10px;
  color: rgb(0, 0, 0); 
}

#to-do-form input::placeholder{
color: rgba(0, 0, 0, 0.5);
}
#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(51, 140, 212);
  border:0;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}